import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioScience from "./portfolioScience/PortfolioScience";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './Science.css';
import scienceData from 'data/unit/Science.json';

class Science extends Component {

  render() {

    const { t, i18n } = this.props;

    return (
      <div className="Science">
        <UnityDescription unity={scienceData} />
        <p style={{ color: scienceData.colorTitles }} className="Science-title-services">{t('unit.services')}</p>
        <div className="Science-container-services">
          <img className="Science-background11-services" src={"https://drive.google.com/uc?id=1LRiZU_JFs_YfPScRMTaLICXE5MQfecwu"} alt={"background11"}></img>
          <div className="Science-container-background2-services">
            <img className="Science-background21-services" src={"https://drive.google.com/uc?id=1VmX6V_m_W_stjFpc4rRmumhzUfbfjWgb"} alt={"background21"}></img>
          </div>
          {
            scienceData.services.map(
              (service, index) => {
                return <div key={index} className="Science-container-service">
                <Service
                service={service}
                color={scienceData.color}
                colorTitle={scienceData.colorSubTitles}
                marcoServices={scienceData.marcoServices}
                />
                </div>
              }
            )
          }
        </div>
        <p style={{ color: scienceData.colorTitles }} className="Science-title-services">{t('unit.portfolio')}</p>
         <div className="Science-container-portfolioCompleted">
          {
            scienceData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="Science-container-background1-portfolioCompleted">
                          <img className="Science-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="Science-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: scienceData.colorTitles }} className="Science-completed-title">{t('unit.completed')}</p>
                        <PortfolioCompleted
                        key={index}
                        index={index}
                        portfolioCompleted={portfolioCompleted}
                        color={scienceData.color}
                        colorTitle={scienceData.colorSubTitles}
                        marcoServices={scienceData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted
                    key={index}
                    index={index}
                    portfolioCompleted={portfolioCompleted}
                    color={scienceData.color}
                    colorTitle={scienceData.colorSubTitles}
                    marcoServices={scienceData.marcoServices}
                    />
                }
              }
            )
          }
        </div>
        <div className="Science-container-portfolioOngoing">
          {
            scienceData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioScience
                key={index}
                index={index}
                portfolioOngoing={portfolioOngoing}
                color={scienceData.color}
                colorTitle={scienceData.colorSubTitles}
                />
              }
            )
          }
        </div>
        <p style={{ color: scienceData.colorTitles }} className="Science-title-services"><Trans i18nKey="unit.techs">1<br />2</Trans></p>
        <div className="Science-container-technologies">
          {
            scienceData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={scienceData.color} />
              }
            )
          }
        </div>
        <p style={{ color: scienceData.colorTitles }} className="Science-title-services">{t('unit.events')}</p>
        <div className="Science-container-events">
          {
            scienceData.events.map(
              (event, index) => {
                return <Event
                key={index}
                event={event}
                color={scienceData.color}
                marcoServices={scienceData.marcoServices}
                />
              }
            )
          }
        </div>
        {/*<RedesUnity redes={scienceData.redes}/>*/}
      </div>
    );
  }
}

export default withTranslation()(Science);
